body {
  margin: 0;
  padding: 0;
}

.disable-select {
  user-select: none; /* supported by Chrome and Opera */
 -webkit-user-select: none; /* Safari */
 -khtml-user-select: none; /* Konqueror HTML */
 -moz-user-select: none; /* Firefox */
 -ms-user-select: none; /* Internet Explorer/Edge */
}


.wrapper {
  position: relative;
  width: 100%;
  margin: 0px;
  /* min-height: 100vh; */
  padding: 0px 10px 10px 10px;
}

.page {
  max-width: 820px;
  min-height: 1060px;
  width: 100%;
  height: 100%;
  margin-top: 10px;
  box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.1);
}

/* CSS PIE DE PAGINA COLORES */
.caja-flotante2 {
  max-width: 821px;
  width: 100%;
}

.cuadrado-naranja{
  -webkit-clip-path: polygon(0 0%, 100% 0, 100% 100%, 8% 100%);
  clip-path: polygon(0 0%, 100% 0, 96% 100%, 8% 100%);
  background-color: #F29205;
  height: 87px;
}

.rounded-caja {
  border-radius: 20px 20px 0px 0px;
}
.rounded-caja-start {
  border-radius: 0px 20px 0px 0px;
}
.rounded-caja-end {
  border-radius: 20px 0px 0px 0px;
}

.visible{
  visibility:visible;
}

.oculto{
  visibility:hidden;
}

.contenedor-linea{
  background: url(/libros_abp/books/ciencias_naturales/5B/reader/build/static/media/linea.b9fb8ec6.svg);
  background-size: 4px 35px;
  background-repeat: repeat;
  background-position: top;

}

.rotulo-etapa{
  width: 120px;
  margin-top: -14px;
  margin-left: 5px;
}

.boton-close{
  color:white !important
}

